  .homepage {
    justify-content: center;
    .art-img-section {margin-top: 20px; margin-left: auto;margin-right: auto;}
    .head-section, .menu-code, .focus, .propositions{margin-left: auto;margin-right: auto;}
    .block-issue {margin-top: 120px;}
  }

  .homepage .menustick-verticalmenu {
    position: fixed;
    top: 0;
    left: 10px;
    right: 20px;
    max-width: 60px;

  }
  .homepage .menustick-newsletter {
    position: fixed;
    top: 20px;
    left: 10px;
    right: 20px;
    cursor:pointer;
    .newsletter {
      transform: rotate(-90deg);
      transform-origin: top right;
      position: absolute;
    	right: 40px;
    	top: 0px;
    }
  }

.homepage {
  .c-block-quote,
  .art-focus-link,
  .focus,
  .block-art{
    margin-top: 30px;
  }
}

@media all and (min-width:700px) {
  .homepage {padding-right: 70px;padding-left: 70px;}
}

.homepage .menu {display: none; margin-left: 76px;position: absolute;}

.homepage .footer {
  margin-top: 180px;
}

.homepage .c-btn1 {
  margin-top: 80px;
  display: block;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  align-items: center;
}

@media all and (max-width:1210px) {
  .block-art .art-img {width: 32%;}
}

@media all and (max-width:925px) {
  .block-art .art-img {width: 48.5%;}
}

@media all and (max-width:540px) {
  .block-art .art-img {width: 100%;height: 150px;margin-bottom: 1rem;.c-atom{top: 10px;}}
  .c-atom {vertical-align: center; margin-top: auto;margin-bottom: auto; }
}

@media all and (max-width:700px) {
  .homepage {
    .drawing-board, .head-section{margin-top: 60px;}
    .search {margin-top: 80px;}
    .menustick-verticalmenu {left: 0px; right: 0px; z-index: 3; max-width: unset;
      -webkit-box-shadow: 0px 10px 37px -20px rgba(0,0,0,0.75);
      -moz-box-shadow: 0px 10px 37px -20px rgba(0,0,0,0.75);
      box-shadow: 0px 10px 37px -20px rgba(0,0,0,0.75);}
    .c-vertical-menu {max-width: unset; justify-content: center; display: flex;align-items: baseline; background-color:white;}
    .c-icon-menu {display: unset; margin-top: 0px; text-align: center; margin-left: 20px; margin-right: 20px; margin-bottom: 6px;}
    .c-atom-section {margin-bottom: 0px;}
    .menustick-newsletter {position: relative; display: flex; margin-right: 10px; left: 5px;
    .newsletter {position: unset; transform: unset; position: initial;}}
  }
    .news-none {.menustick-newsletter{display: none;}}
}
