/*------------------------------------*\
    #TEXT
\*------------------------------------*/

/**
 * Paragraph base styles
 */
p {
  font-size: 11pt;
  font-weight: $font-regular;
  color: $color-neutral-grey3;
  line-height: 17pt;
}

/**
 * Blockquote base styles
 */
blockquote {
  font-style:italic;
  border-left: 1px solid $color-gray-50;
  color: $color-gray-50;
	padding-left: $pad;
	margin-bottom: $space;
}

/**
 * Horizontal rule base styles
 */
hr {
	border: 0;
    height: 2px;
    background: $color-gray-15;
    margin: $space-double 0;
}


/**
 * Abbreviation base styles
 */
abbr {
    border-bottom: 1px dotted $color-gray-50;
    cursor: help;
}

.b-hastags {
  display: flex;
  justify-content: center;
  max-width: 1110px;
  flex-flow: row wrap;
  margin-top: 60px;
}

.tag-search {margin-bottom: 160px; margin-top: 10px;
  .hashtag{background-color: $color-brand-yellow;color: $color-neutral-grey3;}}

.hashtag {
  display: inline-block;
  background-color: $color-brand-beige;
  margin: 5px;
  font-size: 17pt;
	font-weight: $font-extralight;
	color: $color-neutral-grey2;
  padding: 10px;
  padding-left: 50px;
  padding-right: 50px;

  &:hover, &:focus {
    background: $color-brand-yellow;
    color: $color-neutral-grey3;
    }
}

.solved {
    display: inline-block;
    background-color: $color-brand-beige;
    font-size: 15pt;
  	font-weight: $font-light;
  	color: $color-neutral-grey2;
    padding: 2px;
    padding-left: 50px;
    padding-right: 50px;
}


.title-soucis {
	font-size: 20pt;
	font-weight: $font-bold;
	color: $color-neutral-grey3;
  margin-bottom: 6px;
  margin-top: 15px;
}


.forms {
  font-size: 12pt;
  font-weight: $font-light;
  color: $color-neutral-grey3;
  .forms-text {
    padding-left: 10px;
  }
}

#text-forms {
  border-right: 0px solid $color-neutral-grey2;
  border-top: 0px solid $color-neutral-grey2;
  border-left: 0px solid $color-neutral-grey2;
  border-bottom: 0px solid $color-neutral-grey2;
  background-color: white;
  opacity: 1;
  margin-top: 7px;
  &:active {
    border-right: 1px solid $color-neutral-grey2;
    border-top: 1px solid $color-neutral-grey2;
    border-left: 1px solid $color-neutral-grey2;
    border-bottom: 1px solid $color-neutral-grey2;
  }
}

.issue-title {
  font-size: 17pt;
  font-weight: $font-semibold;
  color: $color-neutral-grey2;
  text-align: center;
  margin-top: 20px;
}
