/*------------------------------------*\
    #MENU
\*------------------------------------*/

.menu {
	max-width: 810px;
	display: flex;
	flex-flow: row wrap;
}

.search {
	max-width: 100%;
	display: flex;
}

.c-vertical-menu .search {
	max-width: 810px;
}

.c-vertical-menu {
	.c-icon-menu {
		text-align: center;
	}
	.c-atom {
		margin-left: auto;
		margin-right: auto;
	}
	.verticalmenu-section {
		position: absolute;
		margin-left: auto;
		margin-right: auto;
	}
}

.c-icon-menu {
	display: flex;
	flex-direction: column;
	font-size: 10px;
	color: $color-neutral-grey2;
	margin-top: 12px;
	margin-bottom: 12px;
	cursor: pointer;
	text-align: center;
	position: 0px;
}

.c-icon-menu .c-atom {
	width: 27px;
	padding-right: 2px;
	margin-left: auto;
	margin-right: auto;
}

  .c-atom-section {
		padding-left: 6px;
		padding-right: 6px;
		padding-top: 2px;
		padding-bottom: 2px;
		margin-bottom: 6px;
		margin-top: 12px;
		height: 40px;
		width: 40px;
		margin-left: auto;
		margin-right: auto;
		background-color: $color-brand-beige;
		border-radius: 40px;
  }

.c-icon-menu .c-atom-section {

}

.c-vertical-menu {
	max-width: 60px;
}

.verticalmenu-section {
	margin-top: 15px;
}


.background-section {
	margin-left: auto;
	margin-right: auto;
	height: 40px;
	width: 40px;
	border-radius: 40px;
	background: $color-brand-beige;
}

.c-compo-menu {
	display: flex;
}

.c-compo-searchblock {
	max-width: 830px;
}

.itemmenu {
	margin: 10px;
}

.newsletter {
	display: flex;
	width: 520px;
}

.newsletter textarea, input {
	font-size: 20px;
	opacity: 0.7;
	letter-spacing: 0.02em;
	font-weight: $font-extralight;
}

.menustick-newsletter {
	position: relative;
}

.menustick-verticalmenu {
	position: relative;
}

.menustick-newsletter .newsletter {
	.c-atom {width: 45px;}
}

.menu-code {
	max-width: 1110px;
	display: flex;
	flex-wrap: nowrap;
	.square-level {
	width: -webkit-fill-available;
	}
}

.level1, .level2 {display: inline-flex;width: 100%;}

@media all and (max-width:700px){
	.menu-code {flex-direction: column;}
	.square-level {flex-direction: row; align-items: center; height: 120px;
		.c-atom {margin-left: 0px;margin-right: 0px;}}
}

@media all and (max-width:500px){
	.square-level {padding: 10px;
	.c-atom {height:70px; width:75px;}}
}

@media all and (max-width:350px){
	.level1,.level2 {display: unset;}
}
