.board {
  max-width: 1110px;
  height: 570px;
  background-color: $color-brand-beige;
  cursor: url(../images/icons/pencil.svg) 8 82, auto;
}

@media all and (max-width: 530px) {
  .drawing-board {
    .gribouille {right: unset; left: 20px; bottom: 20px;}
    .button-clear {bottom: 70px; left: 20px;}
  }
}

.drawing-board {
  .board {position: relative;}
  .gribouille {position: absolute;}
  .typo-logo{position: absolute;}
}

.drawing-board {
  position: relative;
  max-width:1110px;
  height: 570px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.gribouille {
  bottom: 20px;
  right: 50px;
  color: $color-neutral-grey2;
  font-weight: $font-light;
  font-style: italic;
  display: flex;
  align-items: flex-end;
}

.gribouille-text {
  margin-right: 30px;
  margin-bottom: 5px;
}

 .gribouille .c-atom {
   width: 15px;
   transform: rotate(30deg);
   border-left: 10px;
 }

 .button-clear {
   position: absolute;
   bottom: 20px;
   left: 40px;
   button {
     width: 150px;
     font-size: 20px;
     background-color: $color-white;
     border: white;
   }
 }
