/*------------------------------------*\
    #FORMS
\*------------------------------------*/

.container-checkbox {
  display: block;
  position: relative;
  padding-left: 45px;
  margin-bottom: 8px;
  cursor: pointer;
  font-size: 14px;
	font-weight: $font-regular;
	color: $color-neutral-grey3;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  top: 2px;
  left: 10px;
  height: 15px;
  width: 15px;
	border: solid 1px $color-neutral-grey2;
	border-radius: 30px;
  background-color: $color-brand-yellow;
}

.container-checkbox:hover input ~ .checkmark {
	background-color: $color-neutral-grey1;
}
.container-checkbox input:checked ~ .checkmark {
  background-color: $color-neutral-grey2;
}

.checkmark:after {
  background-color: $color-neutral-grey2;
  position: absolute;
	display: none;
}
.container-checkbox input:checked ~ .checkmark:after {
  display: block;
}





/**
 * Fieldset base styles
 */
fieldset {
	border: 0;
	padding: 0;
	margin: 0;
}

/**
 * Label base styles
 */
label {
	display: block;
	padding-bottom: 0.2rem;
	font-weight: bold;
}

/**
 * Form button, input, select, and text area base styles
 * 1) Zero out margin and inherit font styles
 */
button, input, select, textarea {
    font-family: "Source Sans Pro";
		color: $color-neutral-grey3;
		font-weight: $font-extralight;
    font-size: 18pt;
    margin: 0;
}

/**
 * Input and text area base styles
 * 1) Width entire container, gray border, padding
 */
input, textarea {
	width: 100%;
	border-left: 0px solid $color-neutral-grey2;
	border-right: 0px solid $color-neutral-grey2;
	border-bottom: 1px solid $color-neutral-grey2;
	border-top: 0px solid $color-neutral-grey2;
	border-radius: $border-radius;
	padding: 0.2rem;

}

/**
 * Input areas base styles
 * 1) Get rid of webkit appearance of these areas
 */
input[type=text], input[type=search], input[type=url], input[type=number], textarea {
	-webkit-appearance: none;
}

/**
 * Button/Submit button base styles
 */
button, input[type="submit"] {
	//padding: $pad-half;
	//background: $color-gray-73;
	//border: 1px solid $color-gray-50;
	cursor: pointer;
}

/**
 * Checkboxes/Radio button base styles
 */
input[type="checkbox"],
input[type="radio"] {
    width: 100px;
//    margin-right: 0.3em;
}


/**
 * Search cancel button and search decoration webkit appearance none
 */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}
