.head-section {
	position: relative;
	max-width: 1110px;
	background-color: $color-brand-beige;
	padding-bottom: 40px;
	.icons-group {
		justify-content: center;
		display: flex;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 30px;
		padding-top: 20px;
		.c-atom {opacity: 1;display: unset;}
	}
	h1 {
		text-align: center;
		vertical-align: center;
		margin-top: 30px;
		margin-bottom: 0px;
		padding-left: 10px;
		padding-right: 10px;
	}
}

.img-section {
	max-width: 1110px;
	height: 230px;
	opacity: 0.3;
	background-image: url("../../images/img_article/bra.png");
	background-position: center;
}

@media all and (max-width:750px){
	.img-section {height:150px;transition: height 0.5s ease;}
}

@media all and (max-width:510px){
	.head-section {.atom1{opacity: 0;transition: opacity 0.7s ease;display: none;}
	h1 {font-size: 50pt;line-height: 50pt;transition: font-size 0.7s ease;}}
}

@media all and (max-width:380px){
	.head-section {.atom2{opacity: 0;transition: opacity 0.7s ease;display: none;}
	h1 {font-size: 30pt;line-height: 30pt;transition: font-size 0.7s ease;}}
}

.icons-group .c-atom{
	margin-right: 15px;
	margin-left: 15px;
}

.sect-focus{
	.art-img {
		max-width: 1110px;
		background-image: url("../images/img_article/carteidentite.jpg");
		width: auto;
		.art-title {
			font-size: 22pt;
			left: 20px;
		}
	}

	.art-subtitle {
		width: 362px;
		font-size: 12pt;
		line-height: 22pt;
		color: $color-brand-beige;
		font-weight: $font-regular;
		left: 20px;
	}

	.art-img-link {
		&:hover {
			.art-title {
	      bottom: 135px;
	    }
		}
	}
}

.sect-focus {
	.art-title {
		transition: bottom 0.5s ease-out;
	}
	.art-subtitle {
		transition: opacity 0.5s 0s ease-in;
	}
}
