.square-pb {
    background: $color-neutral-grey1;
    width: 180px;
    height: 170px;
    justify-content: center;
    margin-top: 0px;
    float: left;
    margin-right: 20px;
    .c-atom {
      width: 130px;
      margin: 20px;
      height: auto;
    }
    &:hover {background: $color-brand-yellow;}
}

.square-level {
    background: $color-neutral-grey1;
    max-width: 1110px;
    height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 20pt;
    font-weight: $font-semibold;
    text-align: center;
    .c-atom {
      width: 100px;
      height: auto;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10px;
    }
    &:hover {
      background: $color-brand-beige;
      transition: background 0.5s ease;
    }
    &:active {
      background: $color-brand-yellow;
      transition: background 0.5s ease;
    }
}

.b-art-soucis {
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1110px;
}

.block-soucis {
  min-height: 170px;
  max-width: 1110px;
  position: relative;
  .solved {
    position: absolute;
    right: 0px;
    top: 0px;
  }
  p {margin-right: 30px;}
  &:hover {
    .square-pb {
      background: $color-brand-yellow;
      transition: background 0.5s ease;
    }
  }
}


// @media all and (max-width:850px){
//   .square-pb {align-items: center; .c-atom {width: 100px; transition: width 0.5s ease;}}
// }

@media all and (max-width:820px){
  .solved {transform: rotate(-90deg); transform-origin: top right;}
  .square-pb {height: 60px; width: 60px; transition: width 0.5s, height 0.5s;
    .c-atom{height: 40px; width: 40px; margin: 10px; transition: width 0.5s, height 0.5s;}
  }
  .solved {margin-right: 30px;}
  .block-soucis {
    p, .title-soucis, .subtitle-soucis {margin-right: 50px;}}
}

.square-soucis {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 155px;
  height: 95px;;
  align-items: center;
  border: $color-brand-yellow solid 2px;
  padding: 5px;
  cursor: pointer;
  &:hover{
    background-color: $color-brand-beige;
    transition: background 0.3s ease;
  }
  &:active {
    background-color: $color-brand-yellow;
    border-color: $color-neutral-grey2;
  }
}


/*------------------------------------*\
    #BLOCKS PROPOSITIONS
\*------------------------------------*/
/**
 * 1) Blocks are collections of text and/or images/media working together as a unit.
 */

.homepage .propositions {margin-top: 80px;}

.propositions {
  background-color: $color-brand-beige;
  max-width: 1110px;
  .title-prop {
    color: $color-white;
    font-size: 15pt;
    font-style: $font;
    font-weight: $font-bold;
    text-shadow: 2px 2px 2px $color-neutral-grey2;
    text-align: center;
    padding-top: 25px
  }
}

.items-prop {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  -webkit-flex-flow: row wrap;
  padding-left: 40px;
  padding-right: 40px;
  .art-img-preview {
    width: 30%;
    margin-bottom: 40px;
  }
}

.block-issue {
  background-color: $color-brand-yellow;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  padding: 15px;
}

.block-issue > * {
    flex: 1 100%;
}

.section-issue {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 25px;
}


.form4 {
  margin-top: 10px;
  margin-bottom: 25px;
  #text-forms {padding-bottom: 100px;}
}

.forms-line {margin-top: 10px;}

.form2{
  padding-left: 20px;
  padding-right: 20px;}

.formbox {display: flex; float: left;}

.formbtn .c-btn2 {
  background: white;
  &:hover {background: white;}
  justify-content: center;
  margin-left: auto;margin-right: auto;}

.c-btn2 .btn-icon .c-atom {opacity: 1;}

.c-btn2 {
    font-size: 14pt;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
    cursor: pointer;
    .c-atom{
      padding-top: 4px;
      margin-left: 10px;
      opacity: 0;
    }
    .btn-text {
      margin-left: 48px;
    }
    &:hover {
      background: white;
      .c-atom {
        opacity: 1;
        transition: opacity 0.5s ease;
      }
      .btn-text {
          text-align: center;
          margin-left: 20px;
          transition: margin-left 1s ease;
      }
    }
  }

@media all {
  .issue-title {order:0;}
  .section-issue {order:1;}
  .forms-line {flex: 1 0 auto; order:2;}
  .form4 {
    order:3;
    width: 1085px;
  }
  .formbox1 {order:4;}
  .formbox2 {order:5;}
  .formbtn {order:6;}
}

@media all and (min-width: 1025px) {
  .container-checkbox {margin-left: 75px;}
  .issue-title {order:0;}
  .section-issue {order:1;}
  .form1 {order:2;max-width: 360px;}
  .form2 {order:2;max-width: 360px;}
  .form3 {order:2;max-width: 360px;}
  .form4 {order:4;max-width: 1085px;}
  .formbox1 {order:5;}
  .formbox2 {order:6;}
  .formbtn {order:7;}
}

@media all and (min-width: 615px) and (max-width: 1025px) {
  .block-issue {padding: 55px;}
  .container-checkbox {margin-left: 10px;}
  .form3 {width: 100%;}
  .form2 {padding-right: 0px;}
}

@media all and (max-width: 615px) {
  .block-issue {padding: 15px;}
  .form2 {padding-left: 0px;padding-right: 0px;width: 100%;}
}

@media all and (max-width: 526px) {
  .section-issue .square-soucis {
    flex-direction: row; height: 90px; width: 50%; padding: 10px; justify-content: center;
    .c-atom {margin-right: 10px;}}
}

@media all and (max-width: 445px) {
  .section-issue .square-soucis {width: 100%;height: 60px;.c-atom{height: 40px;}}
}

/*------------------------------------*\
	#HERO BLOCK
\*------------------------------------*/
.c-block-hero {
    display: block;
    position: relative;
}

/**
 * Hero Image
 * 1) This is the primary hero image that runs full-bleed across the layout
 */
.c-block-hero__img {
    display: block;
    width: 100%;
    max-height: 50vh;
    object-fit: cover;
}

/**
 * Hero Headline
 * 1) This sits overtop of the hero image when space permits
 */
.c-block-hero__headline {
    background: $color-dim-50;
    color: $color-white;
    padding: 1rem;
    font-size: 2.6rem;
    transition: all $anim-speed-quick $anim-ease;

    @media all and (min-width: $bp-large) {
        position: absolute;
        top: 3rem;
        left: 1rem;
        width: 50%;
        border-radius: $border-radius;
    }

    .c-block-hero:hover &, .c-block-hero:focus & {
        background: $color-gray-73;
    }
 }

 /*------------------------------------*\
  #QUOTE BLOCK
 \*------------------------------------*/

.c-block-quote {
  height: auto;
  width: 1110px;
  min-height: 100%;
  color: $color-neutral-grey3;
  font-weight: $font-semibold;
  text-align: center;
  font-size: 34pt;
  padding: 26px;
  margin-left: auto;
  margin-right: auto;
}
 /*------------------------------------*\
 	#MENU BLOCK
 \*------------------------------------*/

 .c-block-menu {
   position: relative;
 	}

  .c-block-menu-enfant {
    height: 190px;
    width: 270px;
    display: table-cell;
    vertical-align: middle;
    background: $color-brand-beige;
    color:$color-neutral-grey2;
    font-weight: $font-semibold;
    text-align: center;
    font-size: 14pt;
    border: 1px solid;
    border-color: $color-white;
    padding: auto;
    border-radius: $border-radius;
    transition: background 0.2s ease;

    &:hover, &:focus {
      background: $color-brand-yellow;
      color: $color-neutral-grey2;
      }
  }

  .c-block-menu-enfant .c-atom {
    height: 82px;
    width: 93px;
  }

  .c-atom {
    height: auto;
    width: auto;
  }


/*------------------------------------*\
	#MEDIA BLOCK
\*------------------------------------*/

/**
 * 1) Media block contains an image (or some other media) on the left
 * and text on the right.
 * 2) For more info, read http://www.stubbornella.org/content/2010/06/25/the-media-object-saves-hundreds-of-lines-of-code/
 */
.c-block-media {
	display: flex;
	transition: all $anim-speed-quick $anim-ease;

	&:hover, &:focus {
		.c-block-media__headline {
			color: $color-brand-green;
		}
	}

	&--small {
		font-size: $font-size-small-2;
	}
}

/**
 * Media Container
 * 1) This is the container that holds the block image (or media)
 */
.c-block-media__media {
	width: 20%;
	max-width: 11em;
	margin-right: 1rem;

	.c-block-media--small & {
		max-width: 3.5em;
		margin-right: 0.5rem;
	}

	@media all and (min-width: $bp-med) {
		width: 50%;
		margin-right: 1.5rem;
	}
}

.c-block-media__img {
	border-radius: $border-radius;
}

.c-block-media__headline {
	margin-bottom: 0.2em;
	color: $color-gray-65;
	transition: all $anim-speed-quick $anim-ease;

	.c-block-media--small & {
		font-size: $font-size-med;
	}
}

.c-block-media__body {
	display: flex;
	flex-direction: column;
	justify-content: center;
	flex: 1;
}

.c-block-media__excerpt {
	color: $color-gray-35;
	transition: all $anim-speed-quick $anim-ease;

	.c-block-media--small & {
		margin-bottom: 0;
		line-height: 1.2;
	}
}


/*------------------------------------*\
	#TOUT BLOCK
\*------------------------------------*/

/**
 * 1) The tout block contains an image and headline overlayed over the image.
 */
.c-block-tout {
    display: inline-block;
	margin-bottom: $space-half;
	position: relative;
    overflow: hidden;
    border-radius: $border-radius;
}

/**
 *  Tout image
 */

 .c-block-tout__img {
	 display: block;
 }

/**
 *  Tout headline
 */
.c-block-tout__headline {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: $color-dim-50;
    color: $color-white;
    padding: $pad-half;
    font-size: 1.4rem;
    transition: all $anim-speed-quick $anim-ease;

    .c-block-tout:hover &, .c-block-tout:focus & {
        padding-bottom: 2rem;
    }
}



/*------------------------------------*\
	#TILE BLOCK
\*------------------------------------*/

/**
 * 1) The tile block contains a span and headline overlayed on a background
 */
.c-block-tile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: $color-dim-50;
    color: $color-white;
    border-radius: $border-radius;
    padding: 1em 1em;
    text-align: center;
    overflow: hidden;
    transition: all $anim-speed-quick $anim-ease;

    &:hover, &:focus {
        background: $color-dim-70;
        color: $color-white;
    }

    @media all and (min-width: 55rem) {
	    padding: 4em 1em;
    }
}

/**
 * Featured tile block
 */
.c-block-tile--featured {
	min-height: 200px;
}

/**
 * Tile block value
 */
.c-block-tile__value {
	font-size: 1.5rem;
    line-height: 1.2;

	/**
	 * Tile value when inside featured tile parent selector
	 */
	.c-block-tile--featured & {
	    font-size: 2.5rem;

	    @media all and (min-width: $bp-med) {
	    	font-size: 5rem;
		}
    }

    @media all and (min-width: 55rem) {
		font-size: 3rem;
    }
}

.c-block-tile__label {
    color: $color-gray-15;
    font-weight: normal;
    font-size: $font-size-med;
}
