
/*------------------------------------*\
    #ARTICLE
\*------------------------------------*/

.block-art {
  display : flex ;
  max-width: 1110px;
  flex-wrap: wrap;
  position: relative;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}


.art-img-vert {
  background-size: 247px auto;
}

.art-img {
  min-width: 247px;
  height: 238px;
  position: relative;
  background-image: url("../images/img_article/cassie.jpg");
  background-position: center;
  margin-bottom: 2rem;
}

.art-img-preview {
  min-width: 250px;
  height: 180px;
  position: relative;
  background-image: url("../images/img_article/snowden.jpg");
  background-position: center;
  .art-filter {
    display: flex;
    min-width: 250px;
    height: 180px;
  }
  .c-atom {
    position: absolute;
    left: 10px;
    top: 20px;
    width: 80px;
    height: auto;
    color: $color-white;
  }
  .art-title-preview {
    position: absolute;
    top: 20px;
    left: 100px;
    margin-right: 15px;
    color: $color-white;
    font-size: 17pt;
    font-style: $font;
    font-weight: $font-semibold;
    text-shadow: 2px 2px 2px $color-neutral-grey3;
  }
  .details {
    width: 110px;
    position: absolute;
    bottom: 10px;
    left: 20px;
    color: $color-white;
    font-size: 10pt;
    font-style: $font;
    font-weight: $font-semibold;
  }
}

.art-img-cub .art-filter {position: absolute;}

.art-img-link{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  &:hover {
    .art-subtitle,
    .c-atom {
      opacity: 1;
    }
    .art-title {
      bottom: 35px;
    }
    .art-filter {
      -webkit-filter: sepia(20%) opacity(75%);
    }
    .art-img-cub .art-filter {
        width: 247px;
        height: 238px;}

  }
}

.art-focus-link {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  &:hover {
    .art-subtitle,
    .c-atom {
      opacity: 1;
    }
    .art-title {
      bottom: 35px;
    }
    .art-filter {
      -webkit-filter: sepia(20%) opacity(75%);
    }
    .p-focus {
      color: $color-brand-beige;
      background-color: rgba(0, 0, 0,0.25);
      transition: background-color 0.7s ease;
    }
  }
}

.art-focus {
  &:hover {
    .art-subtitle,
    .c-atom {
      opacity: 1;
    }
    .art-title {
      bottom: 35px;
    }
    .art-filter {
      -webkit-filter: sepia(20%) opacity(75%);
    }
  }
}

@media all and (min-width: 1215px) and (max-width: 1125px) {
  .focus {.art-focus .art-filter {width: 500px; -webkit-transition: width 2s;}}}

@media all and (min-width: 865px) and (max-width: 1215px) {
  .focus {.art-focus .art-filter {width: 350px; -webkit-transition: width 2s;}}}
// @media all and (min-width: 865px) and (max-width: 770px) {
//   .focus .p-focus {padding-top: 5px;line-height: 19pt;}
// }

@media all and (min-width: 200px) and (max-width: 865px) {
  .art-focus,.art-focus .art-filter {
    display: flex;
    flex-flow: row wrap;
    height:170px;
    order:0;
    }
  .focus .p-focus {height: auto;padding-bottom: 1rem; padding-top: 1em; order:1;}
  .focus {flex-direction: column;}
}

@media all and (max-width: 400px) {
  .art-focus .c-atom {bottom: auto; top: 10px; right: unset;left: 0px;}
}


.art-title {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: $color-white;
  font-size: 17pt;
  font-style: $font;
  font-weight: $font-semibold;
  text-shadow: 2px 2px 2px $color-neutral-grey3;
  transition: bottom 0.2s ease;
}

.art-subtitle {
  position: absolute;
  opacity: 0;
  bottom : 10px;
  left: 10px;
  color: $color-brand-yellow;
  font-size: 13pt;
  font-weight: $font-semibold;
  transition: opacity 0.2s ease;
}


.art-filter {
  position: relative;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: $color-neutral-grey3;
  -webkit-filter: sepia(90%) opacity(30%);
  transition: -webkit-filter 0.4s ease;
}

.art-img .c-atom {
  opacity: 0;
  position: absolute;
  left: 50%;
  transform: translate(-50%,0%);
  top: 20px;
  width: 80px;
  height: auto;
  color: $color-white;
  transition: opacity 0.3s ease;
}

.art-focus .c-atom {
  opacity: 0;
  position: absolute;
  right: 20px;
  bottom: 10px;
  width: 80px;
  height: auto;
  color: $color-white;
  transition: opacity 0.3s ease;
}


.art-focus {
  max-width: 700px;
  height: 238px;
  position: relative;
  background-image: url("../../images/img_article/googlex.jpg");
  background-position: center;

}

.art-focus .art-filter {
  height: 238px;
  width: 700px;
}

.focus {
  display: flex;
  max-width: 1110px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.p-focus {
  height: 238px;
  display: flex;
  font-size: 12pt;
  color: $color-neutral-grey2;
  font-weight: $font-regular;
  line-height: 22pt;
  padding-top: 0.2rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin-top: 0px;
  margin-bottom: 0px;
  background-color: rgba(0,0,0,0);
  transition: background-color 0.2s ease;
  transition: color 0.2s ease;
}

.art-img-section {
  .art-filter {
    position: absolute;}
  .c-atom {
    left: unset; right: 20px; bottom: 10px; top: unset; transform: inherit;}
}

@media all and (max-width: 1140px) {
  .art-img-section {
    width: auto;
    position: relative;
    display: flex;
    flex-flow: row wrap;
    }
  }

  .article {
    width: 1110px;
    height: 480px;
    justify-content: center;
    background-image: url("../../images/img_article/carteidentite.jpg");
    padding-left: 15%; padding-right: 15%;
    h2 {font-size: 50pt; text-align: center; font-weight: $font-bold;text-shadow: 0px 5px 15px rgba(10, 10, 10, 0.5);}
    .c-btn3 { margin-left: auto; margin-right: auto; }
    .date {float: left; color: white; font-size: 15pt; font-weight: $font-bold;text-shadow: 0px 1px 5px rgba(10, 10, 10, 0.5);}
    .name {float: right; color: white; font-size: 15pt;font-weight: $font-bold;text-shadow: 0px 1px 5px rgba(10, 10, 10, 0.5);}
    .hashtag {padding: 2px; padding-left: 30px; padding-right: 30px; font-size: 12pt;}
    .hashtag-art {position: relative; float: left; margin-top: 20px; margin-left: -5px;}
  }
