/*------------------------------------*\
    #HEADINGS
\*------------------------------------*/

/**
 * Heading 1
 */
h1  {
	font-size: 60pt;
	line-height: 55pt;
	color: $color-neutral-grey3;
	font-weight: $font-bold;
}

/**
 * Heading 2
 */
h2 {
	color: $color-white;
	font-size: 17pt;
	font-style: $font;
	font-weight: $font-semibold;
	text-shadow: 2px 2px 2px $color-neutral-grey1;
}

/**
 * Heading 3
 */
h3  {
	color: $color-brand-yellow;
	font-size: 13pt;
	font-weight: $font-semibold;
}

/**
 * Heading 4
 */
h4  {
	font-size: 14px;
	font-weight: $font-light;
	display: flex;
}

/**
 * Heading 5
 */
h5 {

}

/**
 * Heading 6
 */
h6 {

}

/**
 * Subheading
 */
.subheading {
	font-family: $font-secondary;
	font-weight: normal;
}
