/*------------------------------------*\
    #LOGO
\*------------------------------------*/

.c-logo {
	display: block;
	max-width: 200px;

	.c-box__header & {
		max-width: 12em;
		margin: 0 auto;
	}
}

.c-logo-link {
	display: inline-block;
	padding: 0.2rem 1rem;
}

.typo-logo {
	top: 0px;
	left: 40px;
	width: 150px;
	height: 130px;
	background: white;
	padding-left: 15pt;
	padding-right: 15pt;
	padding-top: 9pt;
	color: $color-neutral-grey3;
	font-weight: $font-black;
	line-height: 18pt;
	font-size: 20pt;
	border-radius: 0px 0px 25px 25px ;
}

.typo-logo .logo {
	width: 65px;
	margin-top: 8px;
}
